import React, { useReducer } from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { Line } from 'rc-progress';
import Step1 from './steps/step1';
import SingleMultiSelect from './steps/single-multi';
import FinalStep from './steps/final-step';
import util from './util';

import constants from '../../constants/configurator';
import logoImg from '../../images/nav/logo.svg';
import moneyGraphics from '../../images/configurator/money-graphics.svg';

const Configurator = ({ setBgImg }) => {
  const data = useStaticQuery(query);

  const ALL_PACKAGES = data.allPackages.edges.map(({ node }) => node);
  const [state, dispatch] = useReducer(util.reducer, util.initialState);

  const {
    currentStep,
    step1Results,
    step2Results,
    step3Results,
    step4Results,
    step5Results,
    progressPercentage,
    numberOfSteps,
    goToConfigPackages,
    configPackages,
    tvCount } = state;

  const onButtonNextClick = () => {
    switch (currentStep) {
      case 1: {
        if (step1Results === null) {
          alert('Изберете поне едно от полетата');
          return;
        }
        break;
      }
      case 2: {
        if (step2Results === null) {
          alert('Изберете поне едно от полетата');
          return;
        }
        break;
      }
      case 3: {
        if (step3Results === null) {
          alert('Изберете поне едно от полетата');
          return;
        }
        if (step1Results === 'Само интернет') {
          console.log(step2Results);
          console.log(step3Results);
          console.log(step4Results);
          console.log(step5Results);

          const parsedInternetPackages = util.parseInternetPackage(step2Results, step3Results, ALL_PACKAGES);
          dispatch({
            type: 'final_step',
            configPackages: parsedInternetPackages
          });
          setBgImg(true);
          return;
        }
        break;
      }
      case 4: {
        if (step4Results === null) {
          alert('Изберете поне едно от полетата');
          return;
        }
        break;
      }
      case 5: {
        if (step5Results === null) {
          alert('Изберете поне едно от полетата');
          return;
        }
        
        const parsedPackages = util.parseTvPackage(step2Results,
          step3Results,
          ((tvCount !== ' ' && tvCount !== '') ? [{
            option: tvCount + ' тв',
            isSelected: true
          }] : step4Results),
          step5Results,
          ALL_PACKAGES);
        dispatch({
          type: 'final_step',
          configPackages: parsedPackages
        });
        setBgImg(true);
        break;
      }
    }
    dispatch({ type: 'next_step' });
  }

  const StepComponentList = [
    <Step1
      key={'step1'}
      dispatch={dispatch}
    />,
    <SingleMultiSelect
      key={'step2'}
      dispatch={dispatch}
      dispatchHandle={'step2Results'}
      isSingle={false}
      selectOptions={step2Results}
      title={constants.step2Title}
    />,
    <SingleMultiSelect
      key={'step3'}
      dispatch={dispatch}
      dispatchHandle={'step3Results'}
      isSingle={true}
      selectOptions={step3Results}
      title={constants.step3Title}
    />,
    <SingleMultiSelect
      key={'step4'}
      dispatch={dispatch}
      dispatchHandle={'step4Results'}
      isSingle={true}
      title={constants.step4Title}
      selectOptions={step4Results}
      hasOptionalInput={true}
      tvCount={tvCount}
    />,
    <SingleMultiSelect
      key={'step5'}
      dispatch={dispatch}
      dispatchHandle={'step5Results'}
      isSingle={true}
      title={constants.step5Title}
      selectOptions={step5Results}
    />,
  ]
  return (
    <>
      {goToConfigPackages ? (
        <>
          <FinalStep configPackages={configPackages} />
        </>
      ) : (
          <div className='d-flex justify-content-center' style={{ paddingTop: '12rem' }}>
            <div className='col-12 col-xl-9 p-0 m-0' style={{ minHeight: 1000 }}>
              <div className='row p-0 m-0 d-flex justify-content-center'>
                <LeftSide progressPercentage={progressPercentage} />
                <div className='col-12 col-lg-7 p-0 mt-2 mt-lg-0 p-lg-4 d-flex justify-content-end config-right'>
                  <div className='col-12 col-lg-11 p-0 m-0 py-lg-5 text-center text-lg-left config-card w-100'>
                    <h3>Въпрос <span className='text-green'>{currentStep}</span> от {numberOfSteps}</h3>
                    {/* < Container which renders different steps > */}
                    {StepComponentList[currentStep - 1]}
                    {/* </ Container which renders different steps >*/}

                    <div className='text-center text-lg-left my-5'>
                      <button
                        className={'btn btn-primary green mx-2 my-2'}
                        onClick={onButtonNextClick}>Следваща стъпка</button>
                      {currentStep !== 1 && (
                        <button
                          className='btn btn-secondary mx-2 my-2'
                          onClick={() => dispatch({ type: 'prev_step' })}>Назад</button>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )
      }
    </>
  );
}

export const StepTitle = ({ title }) => {
  return (
    <>
      <h2 className='my-lg-2 text-lg-left text-center'>{title}</h2>
      <hr className='mb-5 center d-block d-lg-none' />
      <hr className='mb-5 d-none d-lg-block' />
    </>
  );
}

export default Configurator;

const LeftSide = ({ progressPercentage }) => {
  return (
    <div className='col-md-6 col-lg-5 d-flex justify-content-center py-5 config'>
      <div className='text-center col-12 col-sm-8 col-md-6 py-md-5'>
        <div className=''>
          <img src={logoImg} className='img-fluid config-logo' alt="NetSurf лого" />
          <button className='btn btn-primary dark-light-blue config-btn d-none d-lg-block'>Асистент</button>
          <p className='text-white mt-4 px-2'>Моля отговорете на въпросите за да създадете най-добрия пакет за вас</p>
        </div>
        <div className='col-12 text-center d-flex justify-content-center flex-column'>
          <img src={moneyGraphics} className='img-fluid' alt="Графика" />
          <div className='d-flex justify-content-between'>
            <h3 className='text-white'>Прогрес</h3>
            <h3 className='text-white'><span className='text-green'>{progressPercentage}%</span></h3>
          </div>
          <Line
            percent={progressPercentage}
            strokeColor="#60feb2"
            trailColor="#FFFFFF"
            strokeWidth={2}
            trailWidth={2}
          />
        </div>
      </div>
    </div>
  )
}

const query = graphql`
  query FinalConfiguratorQuery {
    leftSideBg: file(relativePath: {eq: "configurator/left-side-bg.png"}) {
      childImageSharp {
        fluid(maxHeight: 1000) {
          src
        }
      }
    }
    allPackages: allGoogleSheetTab1Row {
      edges {
        node {
          id
          speed
          package
          tvcount
          duration
          price
          bonuspoints
          pausescuts
          discounts
          individuallity
          timeshift
          archive
          record
        }
      }
    }
  }
`