import React from 'react';
import styled from 'styled-components';
import { StepTitle } from '../configurator';

const SelectCard = styled.div`
  white-space: pre-wrap;
  padding: 1.5rem;
  margin: 1rem 1rem 1rem 1rem;

  display: flex;
  justify-content: start;

  background: #FFFFFF;
  box-shadow: 0px 0px 30px rgba(51, 1, 109, 0.1);
  border-radius: 8px;
  border: ${props => props.isSelected ? '2px solid rgba(96, 254, 178, 1)' : '2px solid transparent'};
  &:hover {
    box-shadow: 0px 0px 30px rgba(96, 254, 178, 1);
  }

  @media screen and (min-width: 991px) {
    margin: 1rem 1rem 1rem 0rem;
  }
`;

const SingleMultiSelect = ({ dispatch, dispatchHandle, isSingle, selectOptions, title, hasOptionalInput, tvCount }) => {
  return (
    <>
      <StepTitle title={title} />
      <div className=''>
        {selectOptions.map(({ option, isSelected }, index) => (
          <SelectCard
            key={index}
            isSelected={isSelected}
            onClick={() => {
              dispatch({
                type: isSingle ? 'update_list_item_single' : 'update_list_item',
                index: index,
                dispatchHandle: dispatchHandle,
                nullTvCount: hasOptionalInput,
              });
            }}
          >
            <div className='d-inline-block'>
              <h3><span className={'mr-2 icon checkbox ' + (isSelected && 'active')}></span>{option}</h3>
            </div>
          </SelectCard>
        ))}

        {hasOptionalInput && (
          <SelectCard
            isSelected={tvCount !== ''}
            onClick={() => {
              dispatch({ type: 'tv_value_change', value: ' ', dispatchHandle: dispatchHandle })
            }}
          >
            <div className='d-inline-block'>
              <h3>
                <span className={'mr-2 icon checkbox ' + (tvCount !== '' && 'active')}></span>
                <input
                  value={tvCount}
                  className='clear'
                  placeholder="Въведи брой"
                  onChange={(v) => {
                    dispatch({ type: 'tv_value_change', value: v.target.value, dispatchHandle: dispatchHandle })
                  }}
                />
              </h3>
            </div>
          </SelectCard>
        )}
      </div>
    </>
  )
}

export default SingleMultiSelect;